@tailwind base;
@tailwind components;
@tailwind utilities;

@keyframes gradient-shift {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes slideIn {
    0% {
        transform: translateX(100%);
        opacity: 0;
    }
    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

.hero-gradient {
    background: linear-gradient(-45deg, #1e40af, #1e3a8a, #3b82f6, #1e40af);
    background-size: 400% 400%;
    animation: gradient-shift 8s ease infinite;
}

.btn-hover-lift {
    @apply transition-all duration-300 transform hover:scale-105 hover:shadow-lg;
}

.text-glow {
    @apply drop-shadow-lg;
}

@layer components {
    .btn-primary {
        @apply px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors font-medium;
    }

    .btn-secondary {
        @apply px-6 py-3 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors font-medium;
    }

    .input-field {
        @apply w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-600 focus:border-transparent;
    }

    .card {
        @apply bg-white rounded-lg shadow-md p-6 hover:shadow-lg transition-shadow;
    }

    .container {
        @apply max-w-6xl mx-auto px-4 sm:px-6 lg:px-8;
    }
}